import React, { useEffect, useState } from "react";
import Layout from '../layout/Layout';
import { makeStyles } from '@material-ui/core/styles';
import { Paper } from '@material-ui/core';
import Typography from '@material-ui/core/Typography';
import { GetSiteConfig } from "../services/BlogService";

const useStyles = makeStyles(() => ({
    root: {
        margin: 20,
        padding: 20,
        height: "80vh",
    },
    rootNoLive: {
        margin: 20,
        padding: 20,
    },
    sectionTitle: {
        paddingBottom: 10
    },
    videoContainer: {
        margin: 20,
        padding: 20,
        position: "relative",
        paddingBottom: "53%",
        height: "0"
    },
    video: {
        position: "absolute",
        top: 0,
        left: 0,
        width: "95%",
        height: "95%"
    }
}));

export default function LivePage() {
    const classes = useStyles();
    const [ytLiveUrlKey, setYTLiveUrlKey] = useState(null);

    useEffect(() => {
        GetSiteConfig()
            .then((response) => {
              if (response !== undefined) {
                var result = response.find(t=>t.rowKey ==='YTLiveUrlKey').value;
                setYTLiveUrlKey(result);                
              }
            });
      }, []);

    return (
        <Layout>
            { !ytLiveUrlKey  ?
                <Paper elevation={3} className={classes.rootNoLive}>
                    <Typography variant="h6" className={classes.sectionTitle}>
                        Bipul Raman is not LIVE right now.
                    </Typography>
                </Paper>
                    : 
                
                    <div className={classes.videoContainer}>
                        <iframe src={"https://www.youtube-nocookie.com/embed/"+ ytLiveUrlKey} frameborder="0" allowfullscreen className={classes.video}></iframe>
                    </div>
                
            }
        </Layout>
    );
}
