import { BlogAPIEndpoint } from "../configs/APIConfig";

export async function BlogCreate(accessToken, data) {
    const headers = new Headers();
    const bearer = `Bearer ${accessToken}`;

    headers.append("Authorization", bearer);
    headers.append("Access-Control-Allow-Origin", "*");
    headers.append("Access-Control-Allow-Headers", "Origin, X-Requested-With, Content-Type, Accept");
    headers.append("Content-Type", "application/json");
    
    const requestOptions = {
        method: "POST",
        headers: headers,
        body: JSON.stringify(data)
    };

    return fetch(BlogAPIEndpoint.Create, requestOptions)
        .then(response => response.json())
        .catch(error => console.log(error));
}

export async function BlogNew(accessToken, formData) {
    const headers = new Headers();
    const bearer = `Bearer ${accessToken}`;

    headers.append("Authorization", bearer);
    headers.append("Access-Control-Allow-Origin", "*");
    headers.append("Access-Control-Allow-Headers", "Origin, X-Requested-With, Content-Type, Accept");
    
    const requestOptions = {
        method: "POST",
        headers: headers,
        body: formData
    };

    return fetch(BlogAPIEndpoint.CreateNew, requestOptions)
        .then(response => response.json())
        .catch(error => console.log(error));
}

export async function BlogDelete(accessToken, blogId) {
    
    const requestOptions = {
        method: 'DELETE',
        headers: { 
            "Access-Control-Allow-Origin": "*",
            "Access-Control-Allow-Headers": "Origin, X-Requested-With, Content-Type, Accept",
            'Authorization': 'Bearer ' + accessToken
        }
    };

    return fetch(BlogAPIEndpoint.Delete + '/' + blogId, requestOptions)
        .then(response => response.json())
        .catch(error => console.log(error));
}

